.container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: fit-content;
}
.container .section {
    margin-bottom: 10%;
    border: 1px solid rgba(245, 245, 245, 1);
    box-shadow: 0px 0px 50px rgba(200, 200, 200, 0.3);
    transform-origin: top;
    /*	transform: scale(1.2);*/
}

.landscape .section {
    width: 297mm;
    aspect-ratio: 1.414;
}

.portrait .section {
    width: 210mm;
    aspect-ratio: 1/1.4135;
}

.section {
    box-sizing: border-box;
}
