.info_container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}

.contact_img {
    width: 1.5rem;
    height: 1.5rem;
}

.contact_container {
    display: flex;
    flex-direction: column;
}

.contact_container > a {
    display: flex;
    align-items: center;
    margin-bottom: 0.3rem;
}

.contact_container > a > img {
    margin-right: 0.5rem;
}
