.container {
    position: absolute;
    top: 50%;
    left: 50%;

    width: 80%;
    height: 50vh;
    min-width: 500px;
    min-height: 500px;

    padding-bottom: 5%;
    box-sizing: border-box;

    overflow-y: hidden;

    border-radius: 0.5rem;

    transform: translate(-50%, -50%);

    background-color: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(30px);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
    z-index: 1;
}

.navigation {
    display: felx;
    align-items: center;

    padding: 0.5rem;

    font-size: 0.8rem;
}

.navigation img {
    height: 0.8rem;
}

.navigation > span {
    margin-left: 0.5rem;
    color: white;
    font-weight: bold;
}

.content {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
}

.content::-webkit-scrollbar {
    display: none;
}
