.container {
    width: 50%;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: center;
    position: relative;
    font-size: 0.75rem;
}

.mainStack {
    list-style: none;
    display: flex;
    flex-direction: column;
    width: 100%;
    min-width: 200px;
}

.legend {
    position: absolute;
    right: 0;
    bottom: 0;

    margin: 1.5rem 0;
    padding-left: 1rem;
    transform: translateX(105%);

    color: #aaa;
}

.title {
    font-size: 1rem;
}
